<template>
  <div class="hello">
    <pk-ecqm-header v-on:logout="logout" notifications='[{ "icon": "social:notifications", "name": "This is a notification", "time": "few sec ago"}]' name="David Smith"></pk-ecqm-header>
  </div>
</template>
<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      data: [{'icon': 'social:notifications', 'name': 'This is a notification', 'time': 'few sec ago'}]
    }
  },
  // define methods under the `methods` object
  methods: {
    logout: function (event) {
      if (window && window.parent) {
        let data = {key : 'logout'};
        window.parent.postMessage(data, '*')
      }
    },
    onWindowMessage: function (e) {
      let data = {};
      if(e.data){
        data = e.data;
      }else{
        return;
      }
      if (e.origin === window.origin && data && data.key === 'changetheme' && data.color) {
        this.changeTheme(data.color);
			}
    },
    changeTheme: function (color) {
      console.log('Changing theme in Header app')
      var link = document.createElement('link');
      link.setAttribute('rel', 'stylesheet');
      link.setAttribute('type', 'text/css');
      link.setAttribute('href', 'static/'+color+'style.css');
      document.getElementsByTagName('head')[0].appendChild(link);
    }
  },
  mounted () {
    // Register an event listener when the Vue component is ready
    window.addEventListener('message', this.onWindowMessage)
  }
}
</script>

